import React from 'react';
import Loading from 'react-loading-animation';
import { connect } from 'react-redux';
import { fetchPosts } from '../actions';
import Search from './Search';
import PostList from './PostList';

class Home extends React.Component {
	constructor(props) {
		super(props);
	}

	componentWillMount() {
		this.props.fetchPosts();
	}
	renderPostLists() {
		if (!this.props.posts.data) {
			return <Loading />;
		}
		return (
				<div>
					<PostList type="sales" title="Nh t bn" posts={this.props.posts.data.filter(post => post.type == "Bn")}/>
					<PostList type="rents" title="Nh t cho thu" posts={this.props.posts.data.filter(post => post.type == "Cho thu")}/>
				</div>
		);
	}
	render() {
		return (
			<div className="">
					<Search />
					{this.renderPostLists()}
			</div>
		)
	}
}

const mapStateToProps = ({posts}) => {
	return { posts }
}

export default connect(mapStateToProps, { fetchPosts })(Home);
